import Link from 'gatsby-link'
import React, { Component } from 'react'

export default class CodeSnipper extends Component {
  constructor() {
    super();
    this.state = {};
  }

  render() {
    return (
      <div>
        <a href="#" onClick={(e) => this._runSnippet(e)}>Run the code</a>
        <code><pre>{ this._fixedContent }</pre></code>
        { this.state.result != undefined ? <code><pre>{this.state.result}</pre></code> : '' }
      </div>
    );
  }

  get _fixedContent() {
    return this.props.content.replace(/^\n/, '');
  }

  _runSnippet(e) {
    e.preventDefault();

    const result = eval(this.props.content);
    this.setState({ result: JSON.stringify(result) });
    console.log(result);
    return false;
  }
}



