import Link from 'gatsby-link'
import React, { Component } from 'react'
import { loadScript } from '../../helpers/inject-script';
import CodeSnippet from '../../components/code-snippet';

export default class Page extends Component {
  componentDidMount() {
    this.unloadScript = loadScript("https://cdnjs.cloudflare.com/ajax/libs/angular.js/1.6.9/angular.js").unload;
  }

  componentWillUnmount() {
    this.unloadScript();
  }

  render() {
    return (
      <div>
        <h1>Notebook - Angular.js</h1>
        <span>
          Based on
          {' '}
          <a href="https://docs.angularjs.org/">angular docs</a>
          {' '}
          and code analysis.
        </span>

        <h2>$injector</h2>
        <h3>Simple</h3>
        <CodeSnippet content={codeSimple}></CodeSnippet>

        <h3>Double injector</h3>
        <CodeSnippet content={codeDouble}></CodeSnippet>

        <h3>Double injector, shared view</h3>
        <CodeSnippet content={codeDoubleWithSharedView}></CodeSnippet>

        <h3>Overwriting services with loadNewModules</h3>
        <span>
          However, this is available only after 1.6.7, and in addition, won't override a service which has been requested already.
          After a service is instantiated, it's placed in <code>cache</code>, to which there's no access, and there's no code to alter its fields.
        </span>
        <CodeSnippet content={codeOverrideBeforeRequested}></CodeSnippet>

        <h3>Overwriting with redeclaration</h3>
        <CodeSnippet content={codeOverrideWithRedeclaration}></CodeSnippet>

        <Link to="/notebook">back to notebook</Link>
      </div>
    );
  }

  _runSnippet(code) {
    eval(code);
    return false;
  }
}

const codeSimple = `
  let module1 = angular.module('module1', []);
  module1.factory('dataService', () => {
    return {
      fn: () => 1,
    };
  });
  let injector = angular.injector(['module1']);
  injector.get('dataService').fn();
`;

const codeDouble = `
  let module1 = angular.module('module1', []);
  module1.factory('dataService', () => {
    return {
      fn: () => 1,
    };
  });
  let injector1 = angular.injector(['module1']);

  let module2 = angular.module('module2', []);
  module2.factory('dataService', () => {
    return {
      fn: () => 2,
    };
  });
  let injector2 = angular.injector(['module2']);

  [
    injector1.get('dataService').fn(),
    injector2.get('dataService').fn()
  ];
`;

const codeDoubleWithSharedView = `
  function renderHtml(injector, template) {
    const $compile = injector.get('$compile');
    const link = $compile(template);

    const $rootScope = injector.get('$rootScope');
    const $scope = $rootScope.$new();

    const fragment = link($scope);
    $scope.$apply();

    return fragment.html();
  }

  let viewModule = angular.module('view', []);
  viewModule.component('viewer', {
    controller: function (dataService) {
      this.data = dataService.fn();
    },
    template: '<span>{{ $ctrl.data }}</span>'
  });

  let module1 = angular.module('module1', []);
  module1.factory('dataService', () => {
    return {
      fn: () => 1,
    };
  });
  let injector1 = angular.injector(['module1', 'ng', 'view']);

  let module2 = angular.module('module2', []);
  module2.factory('dataService', () => {
    return {
      fn: () => 2,
    };
  });
  let injector2 = angular.injector(['module2', 'ng', 'view']);

  [
    renderHtml(injector1, '<viewer></viewer>'),
    renderHtml(injector2, '<viewer></viewer>')
  ];
`;

const codeOverrideBeforeRequested = `
  let module = angular.module('module', []);
  module.factory('dataService', () => {
    return {
      fn: () => 'original-value',
    };
  });

  let injector = angular.injector(['module']);
  injector.loadNewModules([($provide) => {
    $provide.factory('dataService', () => {
      return {
        fn: () => 'overwritten-value',
      };
    });
  }]);

  injector.get('dataService').fn();
`;

const codeOverrideWithRedeclaration = `
  let module = angular.module('module', []);
  module.factory('dataService', () => {
    return {
      fn: () => 'original-value',
    };
  });

  let moduleOverride = angular.module('module2', []);
  module.factory('dataService', () => {
    return {
      fn: () => 'overwritten-value',
    };
  });

  let injector = angular.injector(['module', 'module2']);
  injector.get('dataService').fn();
`;



